import UserItem from '../../../components/UserItem/UserItem.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import UserModel from '@/models/user.js'
const userModel = new UserModel()
export default {
	data() {
		return {
			userData: {
				// name: '张三'
			},
			user:{},
			cashoutList: []
		}
	},
	components: {
		UserItem,
		uniPopup
	},
	mounted(){
		this.UserInfo()
	},
	methods: {
		UserInfo(){
			userModel.getStoreInfo().then(res=>{
				//数据绑定
				const { code, message, data } = res.data
				if(code === '0'){
					this.user = data
					this.userData = data
					console.log(this.userData)
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取用户信息失败'
				})
			})
		},
		setUser(){
			uni.navigateTo({
				url: '../../user/register-seller/register-seller?isModify=1',
				success() {
					uni.setNavigationBarTitle({
						title: '企业信息'
					})
				}
			})
		},
		showDetail(){
			uni.showLoading({
				title: '加载中...'
			})
			userModel.getCashoutDetail().then(res=>{
				uni.hideLoading()
				const {code, message, data } = res.data
				if(code === '0'){
					this.cashoutList = data.list
					this.$refs.recommendRecord.open()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取花费详单失败'
				})
			})
		},
		closeCashoutDialog(){
			this.$refs.recommendRecord.close()
		},
	}
}
